.shop .category{
    padding: 30px;
    height: 100vh;
}
.chead h1{
    font-size: 20px;
    margin-bottom: 20px;
}
.chead h1:nth-child(2){
    opacity: 0.5;
    border-left: 3px solid  rgba(0, 0, 0, 0.5);
    padding-left: 70px;
} 
.shop .category .box{
    background-color: #f6f9fc;
    margin: 15px 0;
    transition: 0.5s;
    border-radius: 5px;
    padding: 5px 10px;
}
/* .shop .category img{
    width: 30%;
    height: auto;
} */
.shop .category .box:hover{
    background-color: #fff;
    box-shadow: rgba(43 52 69/10%) 0 4px 16px;
}
.shop .category .box2{
    margin-top: 80px;
    text-align: center;
}
.shop .category  button{
    padding: 10px;
    background: none;
    font-size: 17px;
    font-weight: 500;
}
.shop .contentWidth{
    margin-left: 30px;
}
.grid1{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap:20px
}
.shop .product img{
    width: 100%;
    height: 100%;
}
.shop .product {
    transition: 0.5s;
  }
  .shop .product:hover {
    box-shadow: rgb(43 52 69 / 10%) 0px 4px 16px;
  }